import { ReservarService } from '../../services/reservar.service';
import { Ticket } from '../../modelos/Ticket.model';
import { TicketResponse } from '../../modelos/Response/TicketResponse.model';
import { GenericResponse } from '../../modelos/Response/GenericResponse.model';
import { Component, OnInit } from '@angular/core';
import {  Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceReserva } from '../../modelos/Servicereserva.model';
import { Reservation } from 'src/app/modelos/Reservas/Reservation.model';
import { RegisterResponse } from 'src/app/modelos/Response/RegisterResponse.model';


@Component({
  selector: 'app-misreservasproveedor',
  templateUrl: './misreservasproveedor.component.html',
  styleUrls: ['./misreservasproveedor.component.css']
})

export class Misreservasproveedor implements OnInit {
  listareserva:Reservation[] = [];
  busqueda:ServiceReserva = new ServiceReserva();
  resultsConsultaReserva$:Observable<GenericResponse<Reservation>>;
  resultsAprobar$:Observable<RegisterResponse>;
  mensaje: any = [];
  isSpinnerVisible: boolean = true;

  page = 1;
  pageSize = 2;

  constructor(private router: Router,
    public reservaservice:ReservarService
    ) {}


  ngOnInit() 
  {
    this.resultsConsultaReserva$ = this.reservaservice.ConsultarMisReservasProveedor();
    this.resultsConsultaReserva$.subscribe(respuesta => 
    {
        this.listareserva = respuesta.JSonResult;
        this.listareserva.slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
        this.isSpinnerVisible = false;
    },
    error => {
        this.isSpinnerVisible = false;
    });

    
  }

  aprobarrechazar(IdReserva,estado)
  {
    this.isSpinnerVisible = true;
    this. resultsAprobar$ = this.reservaservice.Aprobarorechazarreserva(IdReserva,estado)
    this. resultsAprobar$.subscribe(respuesta=>
    {
      if(respuesta.Exitoso === true)
      {
        var index = this.listareserva.findIndex(x=>x.IdReservation == IdReserva);
        this.listareserva[index].ReservationState = estado;
        this.mensaje = [{ id:1, type:'success', message: 'La reserva ha sido ' + (estado == 1 ? 'Aprobada' : 'Rechazada') + ' exitosamente' }];
      }
      else 
      {
        this.mensaje = [{ id:1, type:'danger', message: 'Ha ocurrido un error al momento de aprobar la reserva comuniquese con el administrador' }];  
      }
      
      this.isSpinnerVisible = false;
    },
    error=>
    {
      console.log(error.statusText);
      this.isSpinnerVisible = false;
    });

    
  }
  

  



}
