import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {Observable} from 'rxjs';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';

import { PublicacionesServiceService } from './../../services/Publicaciones-service.service';
import { RegisterResponse } from 'src/app/modelos/Response/RegisterResponse.model';
import { ReservationList } from 'src/app/modelos/Reservas/ReservationList.model';
import { GenericStorageService } from 'src/app/services/genericstorage.service';
import { ReservarService } from 'src/app/services/reservar.service';
import { ReservasNotificacionComponent } from 'src/app/utilidades/ReservasNotificacion/ReservasNotificacion.component';

@Component({
  selector: 'app-resregistro',
  templateUrl: './ResumenReserva.component.html',
  styleUrls: [ './ResumenReserva.component.css']
})

export class ResumenReservaComponent implements OnInit 
{
  public NombreTipoServicio : string;
  results$: Observable<RegisterResponse>;
  @ViewChild(ReservasNotificacionComponent) compReservaNotif: ReservasNotificacionComponent;
  
  public isSpinnerVisible = false;
  static IsEquasError:boolean = false;
  mensaje: any = [];

  InfoReservasActuales: ReservationList[] = [];
  InfoReservaMemoria: ReservationList;
  IdServicio: string;

  constructor(private rutaActiva: ActivatedRoute, private router:Router, config1: NgbProgressbarConfig,
    private genStorageServ : GenericStorageService<ReservationList[]>,
    private ReservasService: ReservarService,
    private servicePublicaciones: PublicacionesServiceService) 
  { 
    this.IdServicio = this.rutaActiva.snapshot.params.IdService;
    this.PrecargarInformacionReserva();

    config1 = new NgbProgressbarConfig();
    config1.max = 80;
    config1.striped = true;
    config1.animated = true;
    config1.type = 'success';
  }

  ngOnInit() 
  {
  }

  PrecargarInformacionReserva()
  {
    this.InfoReservasActuales = this.genStorageServ.getInformation("InfoReservations");   
    if(this.InfoReservasActuales == null)
    {
      this.InfoReservasActuales = [];
    }

    var indexReserva = this.InfoReservasActuales.findIndex(x=>x.IdService == this.IdServicio);
    if(indexReserva == -1 || this.InfoReservasActuales[indexReserva].Reservations == null)
    {
      this.mensaje = [{ id:1, type:'success', message: "Estamos teniendo dificultades. Por favor intente de nuevo o genere un nuevo ticket al area de soporte técnico!" }];
      return;
    }

    this.InfoReservaMemoria = this.InfoReservasActuales[indexReserva];
  }
  
  EnviarReserva()
  {
      this.isSpinnerVisible = true;
      this.results$ = this.ReservasService.crearReserva(this.InfoReservaMemoria);
      this.results$.subscribe(res=>
      {
        this.isSpinnerVisible = false;
        let userResponse: RegisterResponse = res;
          if(userResponse != null && userResponse.Exitoso) 
          {
              this.mensaje = [{ id: 2, type: 'success', message: 'Su reserva se ha realizado exitósamente' }];
              
              var indexReserva = this.InfoReservasActuales.findIndex(x=>x.IdService == this.IdServicio);
              if(indexReserva != -1)
              {
                  this.InfoReservasActuales.splice(indexReserva,1);
                  this.genStorageServ.setInformation("InfoReservations", this.InfoReservasActuales);   
                  this.compReservaNotif.CargarReservasPendientes();
              }

              this.router.navigate(["/reservas/MisReservas"]);        
          }
          else {
              this.mensaje = [{ id: 2, type: 'warning', message: userResponse.MensajeResultado }];
          }
      }, 
      error => 
      {
        this.isSpinnerVisible = false;
        this.mensaje = [{
          id: 2,
          type: 'danger',
          message: error.statusText
        }];
      });
  }
}
