import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RoutesRoutes } from './reservas.routing';
import { RouterModule } from '@angular/router';
import { ReservarService } from '../services/reservar.service';
import { UtilidadesModule } from '../utilidades/utilidades.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { StorageServiceService } from '../services/storage-service.service';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { NgbModule, NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { ReservarCoWorkingComponent } from './ReservarCoWorking/ReservarCoWorking.component';
import { ReservarHotelComponent } from './ReservarHotel/ReservarHotel.component';
import { ReservarAlojamientoComponent } from './ReservarAlojamiento/ReservarAlojamiento.component';
import { MisreservasComponent } from './misreservas/misreservas.component';
import { ResumenReservaComponent } from './ResumenReserva/ResumenReserva.component';
import { Misreservasproveedor } from './misreservasproveedor/misreservasproveedor.component';


@NgModule({
  declarations: [
    ReservarCoWorkingComponent,
    ReservarHotelComponent,
    ReservarAlojamientoComponent,
    MisreservasComponent,
    ResumenReservaComponent,
    Misreservasproveedor
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(RoutesRoutes),
    BsDatepickerModule,
    UtilidadesModule,
    FormsModule,
    ReactiveFormsModule,
    NgbModule
  ],
  providers: [ReservarService, StorageServiceService, NgbProgressbarConfig]
})
export class ReservasModule { }
