import { ReservarService } from './../../services/reservar.service';
import { Ticket } from '../../modelos/Ticket.model';
import { TicketResponse } from '../../modelos/Response/TicketResponse.model';
import { GenericResponse } from '../../modelos/Response/GenericResponse.model';
import { Component, OnInit } from '@angular/core';
import {  Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceReserva } from '../../modelos/Servicereserva.model';
import { Reservation } from 'src/app/modelos/Reservas/Reservation.model';
import { ReservationList } from 'src/app/modelos/Reservas/ReservationList.model';
import { GenericStorageService } from 'src/app/services/genericstorage.service';

//declare const EnviarPago: any;

@Component({
  selector: 'app-misreservas',
  templateUrl: './misreservas.component.html',
  styleUrls: ['./misreservas.component.css']
})

export class MisreservasComponent implements OnInit {
  listareserva:Reservation[] = [];
  busqueda:ServiceReserva = new ServiceReserva();
  resultsConsultaReserva$:Observable<GenericResponse<Reservation>>;
  isSpinnerVisible: boolean = true;
  
  page = 1;
  pageSize = 2;

  constructor(private router: Router,
    private genStorageServ : GenericStorageService<Reservation>,
    public reservaservice:ReservarService
    ) {}


  ngOnInit() 
  {
    this.resultsConsultaReserva$ = this.reservaservice.ConsultarMisReservas();
    this.resultsConsultaReserva$.subscribe(respuesta => 
      {
        this.listareserva = respuesta.JSonResult
        this.listareserva.slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
        this.isSpinnerVisible = false;
      },
      error => 
      {
          this.isSpinnerVisible = false;
      });
  }

  OnFormClick(IdReservation)
  {
      var index = this.listareserva.findIndex(x=>x.IdReservation == IdReservation);
      if(index != -1)
      {
        this.genStorageServ.setInformation("InformacionParaPago", this.listareserva[index]);
        this.router.navigate(["/pagos/pagobitcoint/" + IdReservation]);        
      }      
  }
}
